
import { defineComponent } from "vue";
import { getDonate } from "@front/api/room";
import menu from "@front/directives/menu";
import { menu as menuList } from "@front/components/danmakuFlow/utils/common";
export default defineComponent({
	name: "donateList",
	directives: {
		menu: menu()
	},
	data() {
		const timeout: any = null;
		return {
			list: [],
			timeout,
			currentUser: null
		};
	},
	computed: {
		header(): Array<any> {
			return this.list.slice(0, 3);
		},
		remains(): Array<any> {
			return this.list.slice(3);
		},
		userMenuList(): Array<any> {
			if (!this.currentUser) {
				return [];
			}
			return menuList(
				{
					type: 1001,
					data: this.currentUser
				},
				this.$store
			);
		}
	},
	mounted() {
		this.getList();
		this.$store.commit("getManagerList");
	},
	beforeUnmount() {
		clearTimeout(this.timeout);
	},
	methods: {
		getList() {
			clearTimeout(this.timeout);
			getDonate(this.$store.state.userSession).then(res => {
				this.list = res.map((user: any) => {
					user.isManager = Boolean(
						this.$store.state.managerList.find(
							(i: any) =>
								i.userInfo.userID === user.userInfo.userID
						)
					);
					return user;
				});
			});
			this.timeout = setTimeout(() => {
				this.getList();
			}, 10000);
		}
	}
});
