
import { defineComponent } from "vue";
import { mapState } from "vuex";
export default defineComponent({
	name: "rankList",
	data() {
		const timeout: any = null;
		return {
			timeout,
			currentUser: null
		};
	},
	computed: {
		...mapState(["rank"]),
		rankList(): Array<any> {
			return this.rank.rankList;
		},
		header(): Array<any> {
			return this.rankList.slice(0, 3);
		},
		remains(): Array<any> {
			return this.rankList.slice(3);
		}
	}
});
