
import { defineComponent } from "vue";
import { mapState } from "vuex";
import {
	unixTimeFormatter,
	thousandFormatter
} from "@front/util_function/formatter";
export default defineComponent({
	name: "roomInfo",
	data() {
		return {};
	},
	computed: {
		...mapState(["roomProfile", "roomStatus", "userSession"])
	},
	watch: {},
	methods: {
		unixTimeFormatter,
		thousandFormatter
	}
});
